	module.exports = {"data":{"talkJson":{"description":"\"Speaker: Amy Hanlon\n\nMany of us have experienced a \"\"wat\"\" in Python - some behavior that totally mystifies us. We'll look at three areas where wats arise - identity, mutability, and scope. For each of these three topics, we'll look at some common surprising behaviors, investigate the cause of the behaviors, and cover some practical tips on how to avoid related bugs.\n\nSlides can be found at: https://speakerdeck.com/pycon2015 and https://github.com/PyCon/2015-slides\"","meta":{"duration":"PT24M9S"},"presenter":{"name":null},"slug":"amy-hanlon-investigating-python-wats-pycon-2015","source":{"id":"sH4XF6pKKmk","provider":"youtube"},"tag":[],"title":"Amy Hanlon - Investigating Python Wats - PyCon 2015"},"allTalkJson":null},"pathContext":{"slug":"amy-hanlon-investigating-python-wats-pycon-2015","tags":[]}}

